import React from 'react';
import PropTypes from 'prop-types';
import {Cell, Div} from '@vkontakte/vkui';
import defaultPoster from "../img/defaultPoster.png"

const MoviesCell = (props) => {
debugger
	return (
			<Cell className='moviesCell' onClick={() => {
                props.setIframeUrl(props.movies.iframe_url);
			    props.setActiveModal("MODAL_PAGE_PLAY")
			}}>
                { "material_data" in props.movies && "poster" in props.movies.material_data ? <img className="poser" src={props.movies.material_data.poster || require("../img/defaultPoster.png")} alt='poster'/> : <img src={require("../img/defaultPoster.png")} alt='poster'/>  }
                <div>{props.movies.title_ru}</div>

			</Cell>
	)
}



